$font-stack: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans", "Helvetica Neue", sans-serif;

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/Roboto-Regular.ttf);
}

@font-face {
  font-family: "Museosan";
  font-style: normal;
  font-weight: 300;
  src: url(../fonts/MUSEOSANSCYRL_0.OTF);
}

@font-face {
  font-family: "Museosan";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/MUSEOSANSCYRL_1.OTF);
}

@font-face {
  font-family: "Museosan";
  font-style: normal;
  font-weight: 500;
  src: url(../fonts/MUSEOSANSCYRL_2.OTF);
}

@font-face {
