body {
font-family: Museosan;
  width: 100vw;

  overflow-x: hidden;
  font-size: .8333vw;

  background-color: #fff;
}

.background {
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: -1000;
}

header {
  overflow-x: hidden;
  padding-top: 1.56vw;

}

.content {
  height: 100vh;
  background-color: #fff;
  width: 100vw;
  z-index: 100;
  position: relative;
}

._bold {
  font-weight: bold;
}


.wrapper {
  max-width: 81.25vw;
  width: 100%;
  margin: 0 auto;
}

.topbar {
  display: flex;
  justify-content: space-between;
  padding: 0 0.7vw;
  margin-bottom: .625vw;
}
.logo {
  display: flex;
  align-items: center;
}
.logo_img {
 width: 14.58vw;
 margin-right: 2vw;
}

.logo_desc {
  font-size: .7vw;
  max-width: 7.5vw;
}

.topbar_desc {
  display: flex;
}
.topbar_item {
  display: flex;
  align-items: center;
  margin-right: 3.125vw;
  &._column {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  &__img {
    border: 50%;
    margin-right: 1.3vw;
    max-width: 4.16vw;
    width: 100%;
  }

  &__desc {
    display: flex;
    flex-direction: column;
  }
}
.map_link {
  font-weight: 500;
  text-transform: uppercase;
  font-size: .625vw;
  position: relative;
  margin-top: 1vw;
  box-sizing: content-box;
  color: #ff6078;
  &:hover {
  color: #ff6078;
  }
  &::after {
    content: "";
    border:none;
    border-top:1px dotted #ff6078;
    color:#fff;
    background-color:#fff;
    height:1px;
    width:65%;
    position: absolute;
    bottom: -0.1vw;
    left: 0;
  }
}

.contact_item {
  display: flex;
  align-items: center;
  margin-bottom: .5vw;
  &__img {
    width: 1.77vw;
    height: 1.77vw;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 10px #f8d8c1;
    margin-right: .52vw;
  }
  &__icon {
    width: 0.78vw;
  }
  &__small_text {
    font-size: .625vw;
    position: relative;
    display: flex;
    margin-bottom: .52vw;
    align-items: center;
    span {
      margin-left: .4vw;
    }
  }
  &__phone {
    font-weight: 500;
    font-size: 1.25vw;
  }
}

.btn_registration {
  padding: 1vw 1.3vw;
  border-radius: 30px;
  background-color: #fff;
  box-shadow: 0 3px  0 #ebeaea;
  cursor: pointer;
  img {
    max-width: 1.04vw;
    width: 100%;
    margin-right: .3vw;
  }
  span {
    font-size: .625vw;
  }
}

.green_dot {
  background-color: #27b24b;
  width: .4vw;
  height: .4vw;
  border-radius: 50%;
  box-shadow: 0 0 5px #27b24b;
}

.navbar {
  width: 100%;
  margin-bottom: 5.7vw;
  position: relative;
  z-index: 20;
  background-color: #fff;
}

.nav_list {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 .7vw;
  background-color: #fff;
  border-radius: 5px;
 
  &:after {
    content: "";
    width: 98%;
    left: .78vw;
    height: 10px;
    border-radius: 5px;
    background-color: #f4f4f3;
    position: absolute; 
    bottom: -4px;
    z-index: -10;
  }
}

.nav_link {
  padding: 1.14vw;
  font-size: .78vw;
  font-weight: 300;
  box-sizing: border-box;
  display: block;
  &:hover {
    font-weight: 700;
  }
}


.main_title {
  font-size: 2.5vw;
  max-width: 36.4vw;
  line-height: 1.1;
  margin-bottom: 3.7vw;
}

.services_list {
  margin-bottom: 3.38vw;
}

.services_item {
  margin-bottom: 1vw;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  .point {
    width: 0.625vw;
    height: 0.625vw;
    border-radius: 50%;
    box-shadow: 0 0 5px #ef9499;
    background-color: #fff;
    margin-right: .728vw;
    display: flex;
    align-items: center;
    justify-content: center;
    &::after {
      content: "";
      width: 5px;
      height: 5px;
      background-color: #ef9499;
      border-radius: 50%;
    }
  }
  &__text {
    font-size: .937vw;
    font-weight: 300;
  }
}

.button_image {
  background: rgb(248,178,179);
  background: linear-gradient(45deg, rgba(248,178,179,1) 0%, rgba(246,164,167,1) 51%, rgba(239,141,146,1) 100%);
  padding: 1.45vw 7.8vw 1.45vw 2.34vw;
  border-radius: 30px;
  box-shadow: 0 3px 0 #d7747a;
  position: relative;
  cursor: pointer;
  &__text {
    max-width: 9.3vw;
    font-size: .73vw;
    font-weight: 700;
    text-transform: uppercase;
    color: #fff;
    display: block;
    text-shadow: #c0c0c0;
  }
  &__img {
    position: absolute;
    max-width: 10.4vw;
    right: -3.5vw;
    bottom: 8px;
  }
}

.background-woman {
  position: absolute;
  right: 0;
  bottom: -8vw; 
  max-width: 45vw;
  width: 100%;
  z-index: -99;
}

.head {
  position: relative;
  margin-bottom: 10vw;
}

.head_shape-1 {
  position: fixed;
  top: 27%;
  z-index: -100;
  left: 4%;
  max-width: 14.5vw;
}

.head_shape-2 {
  position: absolute;
  top: 60.33%;
  z-index: -100;
  left: 49%;
  z-index: 12;
}

.head_shape-3 {
  position: fixed;
  top: 11.11%;
  z-index: -100;
  right: -5%;
  z-index: -100;
  max-width: 10vw;

}

.map_link:hover+.popup_map {
  display: block;
}
.topbar_item__desc {
  position: relative;
}
iframe {
  width: 15.6vw;
  height: 15.6vw;
}
.popup_map {
  display: none;
  position: absolute;
  bottom: -17vw;
  left: 0;
  z-index: 100;
}

.client_card {
  position: absolute;
  left: 43%;
  bottom: 0;

  &__img {
    max-width: 9.6vw;
    width: 100%;
    z-index: 10;
  }
  &__desc {
    cursor: pointer;
    bottom: 25%;
    position: absolute;
    right: 0%;
    transform: translate(50%);
    width: 3vw;
    z-index: 20;
  }
  &__icon {
    max-width: 3vw;
    width: 100%;
    position: absolute;
    z-index: 20;
    &:hover {
      &+.client_card__text {
        opacity: 1;
        z-index: 10;
        position: fixed;
      }
    }
  }
  &__text {
    position: absolute;
    bottom: -1vw;
    left: 1vw;
    padding: 1.25vw;
    background-color: #fff;
    width: 9.1vw;
    height: 10vw;
    border-radius: 20px 20px 10px 10px;
    box-shadow: 0 0 2px #f9f6f4;
    opacity: 0;
    transition: .3s;
    &::after {
      content: "";
      position: absolute;
      bottom: 5%;
      left: 5%;
      z-index: -10;
      padding: 1.25vw;
      background-color: #fff;
      width: 9.1vw;
      height: 10vw;
      opacity: .4;
      border-radius: 20px 20px 10px 10px;
    }
  }
}

.wave_cont {
  position: absolute;
  bottom: -11.11vw;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 9.11vw;
  background-image: url(/img/wave.png);
  background-size: contain;
  background-repeat: no-repeat;
  .pulse_arrow {
    position: absolute;
    left: 50%;
    top: 3%;
    width: 1.56vw;
    transform: translateX(-50%);
    cursor: pointer;
    img {
      width: 100%;
    }
    &::after, &::before {
      content: "";
      position: absolute;
      border: 1px solid #fff;
      left: -1.9vw;
      /* right: -16px; */
      /* bottom: -20px; */
      top: -2vw;
      transform: translate(-50%, -50%);
      display: block;
      border-radius: 50%;
      animation: pulse 1.8s linear infinite;
      width: 5vw;
      height: 5vw;
      
    }
    &::after {
      animation-delay: .5s;
    }
  }
}

@keyframes pulse {
  0% {
    transform: scale(0.5);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(1.2);
    opacity: 0;
  }
}

body.popup-show {
  overflow: hidden;
  .popup {
    opacity: 1;
    z-index: 1000;
  }
}

.popup {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  z-index: 10000;
  opacity: 0;
  transition: .3s;
  z-index: -1;
  &__close {
    content: "";
    width: 14px;
    height: 14px;
    position: absolute;
    top: 4%;
    right: 4%;
    cursor: pointer;
  }
  .popup_window {
   
    padding: 3.9vw 5.2vw;
    max-width: 25.2vw;
    display: flex;
    flex-direction: column;
    border-radius: 30px;
    background: rgb(252,253,253);
    position: relative;
    align-items: center;
    background: linear-gradient(45deg, rgba(252,253,253,1) 0%, rgba(249,235,229,1) 100%);
  }
  &__title {
    font-size: 1.56vw;
    margin-bottom: 1.56vw;
  }
  &__text {
    text-align: center;
    margin-bottom: 2vw;
  }
  &__form {
    display: flex;
    flex-direction: column;
  }
  &__field {
    padding: 1.56vw 2.3vw;
    font-size: .8vw;
    background-color: #fff;
    border-radius: 40px;
    border: none;
    margin-bottom: 1.04vw;
    z-index: 101;
    width: 100%;
    box-sizing: border-box;
  }
  &__image {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(-30%, -30%);
    max-width: 13.54vw;
    z-index: 100;
  }
}

body.popup_success-show {
  overflow: hidden;
  .popup {
    opacity: 1;
    z-index: 1000;
  }
  .popup_window {
    display: none;
  }
  .popup_success {
    display: flex;
  }
}
.popup_success {
  background: rgb(252,253,253);
  background: linear-gradient(45deg, rgba(252,253,253,1) 0%, rgba(249,235,229,1) 100%);
  padding: 3.9vw 5.2vw;
  display: none;
  flex-direction: column;
  align-items: center;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
  &__image {
    max-width: 5vw;
    margin-bottom: 1.56vw;
  }
  &__title {
    font-weight: bold;
    font-size: 2.6vw;
    margin-bottom: .78vw;
  }
  &__text {
    font-size: 2vw;
  }
  &__bg {
    position: absolute;
    bottom: 0%;
    right: 0%;
    transform: translate(64%, 30%);
    width: 14vw;
  }
}


.hamburger {
  width: 50px;
  height: 50px;
  background-color: #fff;
  border-radius: 5px;
  position: fixed;
  top: 3%;
  right: 5%;
  z-index: 100;
  display: none;
}

